import React from "react";
import "./Production.css";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Cascader } from "antd";
import { DatePicker, Space } from "antd";
import { Button, Form, Input, Select, Upload } from "antd";

const { TextArea } = Input;

function Production() {
  const [formValues, setFormValues] = useState({
    nameOrder: "",
    category: "",
    desc: "",
    quantity: "",
    materialOrder: "",
    sample: "",
    delivery: "",
    orderDeadline: "",
    payment: "",
    moreServices: "",
    comments: "",
  });

  const [showNewOrder, setshowNewOrder] = useState(false);
  const navigate = useNavigate();

  const onChange = (value) => {
    console.log(`selected ${value}`);
  };

  const [status, setStatus] = useState("");

  const onChangeSelected = (e) => {
    setFormValues((formValues) => {
      return {
        ...formValues,
        [e.target.name]: e.target.value,
      };
    });
  };

  const onChangeCascad = (value) => {
    console.log(value);
  };

  const onClickBackNewOrder = () => {
    setshowNewOrder(true);
    navigate("/neworder");
  };

  const optionsVal = [
    {
      value: "",
      label: "",
      children: [
        {
          value: "",
          label: "",
          children: [
            {
              value: "xihu",
              label: "West Lake",
            },
          ],
        },
      ],
    }]

  const { RangePicker } = DatePicker;

  const onFinish = async (e) => {
    // e.preventDefault();

    const token = "6059462033:AAHMTNU6CakxUuMjoaiayqgkAN1R-cyxQ-A";
    const chat_id = "-1001950653999"; //    ,https://api.telegram.org/botXXXXXXXXXXXXXXXXXXXXXXX/getUpdates,
    // , XXXXXXXXXXXXXXXXXXXXXXX -   ,  
    const url =
      "https://api.telegram.org/bot" +
      token +
      "/sendMessage?chat_id=" +
      chat_id +
      "&parse_mode=html&text=" +
      encodeURIComponent(
        " : " +
          formValues.nameOrder +
          "\n : " +
          formValues.desc +
          "\n: " +
          formValues.category +
          "\n: " +
          formValues.quantity +
          "\n : " +
          formValues.payment +
          "\n : " +
          formValues.moreServices +
          "\n  : " +
          formValues.comments +
          "\n  : " +
          "google.com"
      );
    try {
      const response = await fetch(url);
      const data = await response.json();
      if (data.ok) {
        setStatus("  ");
      } else {
        setStatus("");
      }
    } catch (error) {
      console.error(error);
      setStatus("");
    }
  };

  return (
    <>
      <div className="production-container">
        <div className="production-content">
          <h3>:</h3>
          <Form
            labelCol={{
              span: 15,
              color: "red",
            }}
            wrapperCol={{
              span: 25,
            }}
            layout="vertical"
            style={{
              maxWidth: 600,
            }}
            onFinish={onFinish}
          >
            <Form.Item label=" :">
              <Input
                placeholder=":  "
                name="nameOrder"
                onChange={onChangeSelected}
              />
            </Form.Item>
            <Form.Item label=":">
              <Select
                name="category"
                placeholder=" "
                // onChange={onChangeSelected}
                options={[
                  { value: "female", label: " " },
                  { value: "male", label: " " },
                  { value: "kids", label: " " },
                  { value: "specialWear", label: "" },
                ]}
              />
            </Form.Item>
            <Form.Item label=" :">
              <TextArea
                placeholder=":    "
                rows={5}
                name="desc"
                onChange={onChangeSelected}
              />
            </Form.Item>
            <Form.Item label=":">
              <Input
                type="number"
                name="quantity"
                onChange={onChangeSelected}
              />
            </Form.Item>
            <Form.Item label="  :">
              <Select
                name="materialOrder"
                placeholder=" "
                onChange={onChange}
                options={[
                  { value: "customer", label: " " },
                  { value: "performer", label: " " },
                  { value: "agreement", label: " " },
                ]}
              />
            </Form.Item>
            <Form.Item label=":">
              <Select
                name="sample"
                placeholder=" "
                onChange={onChange}
                options={[
                  { value: "customer", label: " " },
                  { value: "agreement", label: " " },
                ]}
              />
            </Form.Item>
            <Form.Item label=":">
              <Cascader
                name="delivery"
                options={optionsVal}
                onChange={onChangeCascad}
              />
            </Form.Item>
            <Form.Item label="  :">
              <Space direction="vertical" size={12}>
                <RangePicker
                  name="orderDeadline"
                  placeholder={[" ", " "]}
                  format="DD-MM-YYYY"
                />
              </Space>
            </Form.Item>
            <Form.Item label=" :">
              <Input
                name="payment"
                placeholder=", ,    .."
                onChange={onChangeSelected}
              />
            </Form.Item>
            <Form.Item label=" :">
              <Input
                name="moreServices"
                placeholder=": ,   .."
                onChange={onChangeSelected}
              />
            </Form.Item>
            <Form.Item label="  :">
              <TextArea
                name="comments"
                placeholder=":    "
                rows={3}
                onChange={onChangeSelected}
              />
            </Form.Item>

            <Button
              type="primary"
              htmlType="submit"
              className="btn-production-send-form"
              style={{ marginBottom: "10px" }}
            >
                
            </Button>
          </Form>
          <div className="btns-production-services">
            <Button
              type="primary"
              className="btn-production-back"
              onClick={onClickBackNewOrder}
            >
              
            </Button>
          </div>
          {showNewOrder && navigate("/neworder")}
        </div>
      </div>
    </>
  );
}

export default Production;
